<template>
  <div>
    <div id="headTop">
      <slot class="left" name="left"></slot>
      <span class="header_title" @click="titleClick">{{title}}</span>
      <!-- <span
        class="log"
        @click="clickCount > 1 ? (showLog = true) : clickCount++"
      >&nbsp;&nbsp; &nbsp;&nbsp; &nbsp;&nbsp;</span> -->
      <slot></slot>
      <span v-if="showBack" @click="goBack" class="head-back">
        <i class="iconfont icon-fanhui"></i>
      </span>

      <slot class="a" name="right"></slot>
    </div>
    <div class="head_seat"></div>

    <!-- <van-popup v-model="showLog" position="bottom" :style="{ maxHeight: '80%', minHeight: '20%' }">
      <div v-if=" $store.getters.errorLogs">
        <div v-for="(item, index) in $store.getters.errorLogs" :key="index">item.info</div>
      </div>
      <div v-else>无数据</div>
    </van-popup> -->
  </div>
</template>

<script>
// import { Popup } from "vant";
export default {
  name: "",
  // components: { [Popup.name]: Popup },
  props: {
    title: String,
    showBack: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
          clickCount: 0,
    showLog: false,
    };
  },
  computed: {},
  watch: {},
  created() {
    if (this.title === "我的首页" && this.title === "首页课程") {
      if ($(".head-back").length > 0) {
        $(function() {
          $(".head-back").css("display", "none");
        });
      }
    }
  },
  methods: {
    goBack() {
      // 指定返回页，则跳转指定页
      if (this.$route.meta && this.$route.meta.from) {
        this.$router.replace(this.$route.meta.from);
      } else {
        this.$router.go(-1);
      }
    },
    titleClick() {
      this.$emit("touchClick", null);
      // this.clickCount > 8 ? (this.showLog = true) : this.clickCount++
    }
  }
};
</script>

<style lang='less'>
@height: 4.3rem;
#headTop {
  width: 100%;
  height: @height;
  background: rgba(0, 203, 171, 1);
  position: fixed;
  top: 0;
  z-index: 9999;
  .left {
    width: 20px;
    padding: 20px;
    border: 1px solid #000;
  }
  .header_title {
    width: 100%;
    background: rgba(0, 203, 171, 1);
    text-align: center;
    color: #fff;
    font-size: 1.4rem;
    line-height: @height;
  }
  .right {
    float: right;
    margin-right: 4vw;
    font-size: 1.5rem;
    color: #fff;
    line-height: @height;
  }
}
.head_seat {
  width: 100%;
  height: @height;
  background: rgba(0, 203, 171, 1);
}
</style>


