<template>
  <div id="app">
    <!-- <r-spin v-show="$store.getters.loading"></r-spin> -->
    <r-loading v-show="$store.getters.requestNum > 0"></r-loading>
    <!-- <keep-alive> -->
    <!-- <router-view /> -->
    <!-- <keep-alive :max="10"> -->
      <router-view></router-view>
    <!-- </keep-alive> -->
    <!-- <router-view v-if="!$route.meta || !$route.meta.cache"></router-view> -->
    <!-- </keep-alive> -->
  </div>
</template>

<script>
import RSpin from "./components/RSpin";
import RLoading from "./components/RLoading";
import interflow from "./mixin/interflow";
import note from "./mixin/note";
import { FuncType, sendToAPP } from "@/utils/interFlow";
import { LimitType } from "@/utils/enum";
export default {
  name: "App",
  components: { RSpin, RLoading },
  mixins: [interflow, note],
  data() {
    return {
      clientHeight: document.documentElement.clientHeight,
      timeStamp: 0
    };
  },
  created() {
    // //监听页面刷新
    // window.addEventListener("beforeunload", function() {
    //   //监听刷新默认缓存当前组件
    //   that.$store.commit("resetcachePageName", that.$route.name);
    // });
  },
  mounted() {
    // 如果支持 popstate 一般移动端都支持了
    if (window.history && window.history.pushState) {
      // 往历史记录里面添加一条新的当前页面的url
      history.pushState(null, null, document.URL);
      // 给 popstate 绑定一个方法 监听页面刷新
      window.addEventListener("popstate", this.backChange, false); //false阻止默认事件
    }
  },
  methods: {
    backChange(e) {
      if (
        this.$route.meta &&
        this.$route.meta.limits &&
        this.$route.meta.limits.indexOf(LimitType.top) > -1
      ) {
        console.log("当前是顶级了，无法再后退");
        window.history.go(1);
        if (e.timeStamp - this.timeStamp < 50) {
          sendToAPP(FuncType.closeAPP, "");
        }
        this.timeStamp = e.timeStamp;
      } else {
        // 物理按键返回键处理
        if (this.$route.meta && this.$route.meta.from) {
          this.$router.replace(this.$route.meta.from);
        } else {
          //点击返回就默认返回上一页，这里不处理，处理了会有问题，默认的返回上一页就行
          // this.$router.go(-1);
        }
      }
    },
    getAllItem(values) {
      let arr = [];
      values.map(item => {
        if (item.children && item.children.length > 0) {
          let arrs = this.getAllItem(item.children);
          arr.push(...arrs);
        } else if (item.meta && item.meta.cache) {
          arr.push(item.name);
        }
        // else if (item.meta && item.meta.cache) {
        //   return item.name;
        // }
      });
      return arr;
    }
  },
  computed: {
    cachePageName() {
      let array = this.getAllItem(this.$router.options.routes);
      console.log("array:", array);
      return array;
    }
  },
  destroyed() {
    window.removeEventListener("popstate", this.backChange, false); //false阻止默认事件
  }
};
</script>

<style>
#app {
  width: 100vw;
  height: 100%;
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  background: #ebf0ef;
}

html,
body {
  width: 100%;
  height: 100%;
  /* font-size: 12px; */
  color: #666;
}

html {
  font-size: 62.5%;
  /* 10÷16=62.5% */
}

body {
  /* font-size:12px; */
  font-size: 1.2rem;
  /* 12÷10=1.2 */
}

@media only screen and (min-width: 481px) {
  html {
    font-size: 94% !important;
  }
}

@media only screen and (min-width: 561px) {
  html {
    font-size: 109% !important;
  }
}

@media only screen and (min-width: 641px) {
  html {
    font-size: 125% !important;
  }
}
</style>



