<template>
  <div id="foot-bar">
    <van-tabbar @change="tgTab" v-model="FootActive">
    
      <!-- 暂无数据 隐藏 -->
      <!-- <van-tabbar-item to="/homePage" replace>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ?require('../assets/icon_sy_pressed.png'): require('../assets/icon_sy_normal.png')"
        />
        <span>首页</span>
      </van-tabbar-item> -->
      <van-tabbar-item to="/homeCourse" replace>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ?require('../assets/icon_kc_pressed.png'): require('../assets/icon_kc_normal.png')"
        />
        课程
      </van-tabbar-item>
      <van-tabbar-item to="/myZone" replace>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ?require('../assets/icon_wd_pressed.png'): require('../assets/icon_wd_normal.png')"
        />
        我的
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { Tabbar, TabbarItem } from "vant";
export default {
  name: "",
  components: { [Tabbar.name]: Tabbar, [TabbarItem.name]: TabbarItem },
  props: {},
  data() {
    return {
      FootActive: ""
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {
    let path = this.$route.path;
    // console.log(path, "路径");
    // if (path === "/homePage") {
    //   this.FootActive = 0;
    // }
    if (path === "/homeCourse") {
      this.FootActive = 0;
    }
    if (path === "/myZone") {
      this.FootActive = 1;
    }
  },
  methods: {
    tgTab(val) {
      // console.log(val);
    }
  }
};
</script>

<style lang='less'>
#foot-bar {
  z-index: 999;
  width: 100%;
  height: 5rem;
  position: absolute !important;
  bottom: 0;
  z-index: 999;
  overflow: hidden;
  .van-tabbar-item--active {
    color: #00cbab;
  }
}
</style>


