const getters = {
  cachePageName: state => state.user.cachePageName,

  loading: state => state.user.loading,
  requestNum: state => state.user.requestNum,
  localIP: state => state.user.localIP,
  userId: state => state.user.userId,
  userName: state => state.user.userName,
  userPassword: state => state.user.userPassword,
  serverIP: state => state.user.serverIP,
  gatewayIP: state => state.user.gatewayIP,
  recIP: state => state.user.recIP,

  student: state => state.user.student,
  studentID: state => state.user.studentID,
  course: state => state.user.course,
  courseID: state => state.user.courseID,
  group: state => state.user.group,
  groupID: state => state.user.groupID,
  groupIP: state => state.user.groupIP,
  groupType: state => state.user.groupType,

  screenQuestion: state => state.user.screenQuestion,
  classPractice: state => state.user.classPractice,
  groupDiscussion: state => state.user.groupDiscussion,
  boardNote: state => state.user.boardNote,

  noteRefresh: state => state.user.noteRefresh,
  liveLession: state => state.user.liveLession,
  errorLogs: state => state.errorLog.logs
};

export default getters;



