import {
  removeToken,
  tokens,
  setLocalStorage,
  getLocalStorage,
  setSession,
  getSession,
  removeSession
} from "@/utils/auth";
import mainHttp from "@/api/mainServer";
import gatewayHttp from "@/api/gatewayServer";
import { default as gatewaySignalR } from "@/mixin/gatewaySignalR";
import { GroupType } from "@/utils/enum";

const user = {
  state: {
    cachePageName: "", //缓存组件名称

    loading: false, // 页面加载状态
    requestNum: 0, // 当前请求数量
    localIP: getLocalStorage(tokens.localIP),
    userId: getLocalStorage(tokens.userId),
    userName: getLocalStorage(tokens.userName),
    userPassword: getLocalStorage(tokens.userPassword),
    serverIP: getLocalStorage(tokens.serverIP),
    gatewayIP: getLocalStorage(tokens.gatewayIP),
    recIP: getLocalStorage(tokens.recIP),

    student: getLocalStorage(tokens.student, true),
    studentID: getLocalStorage(tokens.StudentID),

    course: getSession(tokens.course, true),
    courseID: getSession(tokens.courseID),
    group: getSession(tokens.group, true),
    groupID: getSession(tokens.groupID),
    groupIP: getSession(tokens.groupIP),
    groupType: getSession(tokens.groupType),

    screenQuestion: {},
    classPractice: {},
    groupDiscussion: {},
    boardNote: {},

    noteRefresh: false,

    // 临时课堂实录信息
    liveLession: null
  },
  mutations: {
    //重置缓存组件名称
    resetcachePageName(state, res) {
      state.cachePageName = res;
    },
    //添加缓存组件名称
    addcachePageName(state, res) {
      if (state.cachePageName == "") {
        state.cachePageName = res;
      } else {
        let arr = state.cachePageName.split(",");
        if (res && typeof res === "string") {
          let i = arr.indexOf(res);
          if (i <= -1) {
            state.cachePageName = state.cachePageName + "," + res;
          }
        }
      }
    },
    //删除缓存组件名称
    delcachePageName(state, res) {
      let arr = state.cachePageName.split(",");
      if (res && typeof res === "string") {
        let i = arr.indexOf(res);
        if (i > -1) {
          arr.splice(i, 1);
          state.cachePageName = arr.join();
        }
      }
    },
    SET_LOADING: (state, value) => {
      state.loading = value;
    },
    SET_REQUEST: (state, value) => {
      state.requestNum += value;
    },
    SET_IP: (state, ip) => {
      state.localIP = ip;
      setLocalStorage(tokens.localIP, ip);
    },
    SET_KEY: (state, userId) => {
      state.userId = userId;
      setLocalStorage(tokens.userId, userId, 1000 * 60 * 60 * 12);
    },
    SET_SERVERIP: (state, serverIP) => {
      state.serverIP = serverIP;
      setLocalStorage(tokens.serverIP, serverIP);
    },
    SET_GATEWAYIP: (state, gatewayIP) => {
      state.gatewayIP = gatewayIP;
      setLocalStorage(tokens.gatewayIP, gatewayIP);
    },
    SET_RECIP: (state, recIP) => {
      state.recIP = recIP;
      setLocalStorage(tokens.recIP, recIP);
    },
    SET_USER: (state, userName) => {
      state.userName = userName;
      setLocalStorage(tokens.userName, userName);
    },
    SET_USERPASSWORD: (state, userPassword) => {
      state.userPassword = userPassword;
      setLocalStorage(tokens.userPassword, userPassword);
    },
    SET_STUDENT: (state, student) => {
      state.student = student;
      setLocalStorage(tokens.student, student);
    },
    SET_STUDENTID: (state, StudentID) => {
      state.studentID = StudentID;
      setLocalStorage(tokens.StudentID, StudentID);
    },
    SET_COURSE: (state, course) => {
      state.course = course;
      setSession(tokens.course, course);
    },
    SET_COURSEID: (state, courseID) => {
      state.courseID = courseID;
      setSession(tokens.courseID, courseID);
    },
    SET_GROUP: (state, group) => {
      state.group = group;
      setSession(tokens.group, group, 1000 * 60 * 60 * 2);
    },
    SET_GROUPIP: (state, groupIP) => {
      state.groupIP = groupIP;
      setSession(tokens.groupIP, groupIP, 1000 * 60 * 60 * 2);
    },
    SET_GROUPID: (state, groupID) => {
      const oldGroupId = state.groupID;
      state.groupID = groupID;
      setSession(tokens.groupID, groupID, 1000 * 60 * 60 * 2);
      if (oldGroupId !== groupID && oldGroupId) {
        window.GatewayHub.invoke("QuitGroup", oldGroupId)
          .then(res => {
            console.log("退出讨论组：", groupID);
          })
          .catch(err => {
            console.log("err：", err);
          });
      }

      setTimeout(() => {
        window.GatewayHub.invoke("JoinGroup", groupID)
          .then(res => {
            console.log("加入讨论组：", groupID);
            gatewaySignalR.initGroupInfo(true);
          })
          .catch(err => {
            console.log("err：", err);
          });
      }, 500);
    },
    QUIT_GROUP: state => {
      let groupID = state.groupID;
      if (groupID) {
        window.GatewayHub.invoke("QuitGroup", groupID)
          .then(res => {
            console.log("退出讨论组：", groupID);
          })
          .catch(err => {
            console.log("err：", err);
          });
      }
      state.group = "";
      state.groupIP = null;
      state.groupID = null;
      setSession(tokens.groupID, "", 1000 * 60 * 60 * 2);
      setSession(tokens.groupIP, null, 1000 * 60 * 60 * 2);
      setSession(tokens.group, null, 1000 * 60 * 60 * 2);
      gatewaySignalR.initGroupInfo(false); // 更新分组信息给APP
    },

    SET_GROUPTYPE: (state, groupType) => {
      state.groupType = groupType;
      setSession(tokens.groupType, groupType);
    },

    SET_SCREENQUESTION: (state, value) => {
      state.screenQuestion = value;
    },
    SET_CLASSPRACTICE: (state, value) => {
      state.classPractice = value;
    },
    SET_GROUPDISCUSSION: (state, value) => {
      state.groupDiscussion = value;
    },
    SET_BOARDNOTE: (state, value) => {
      state.boardNote = value;
    },

    SET_NOTEREFRESH: (state, value) => {
      state.noteRefresh = value;
    },

    SET_LIVELESSION: (state, value) => {
      state.liveLession = value;
    },

    /**
     * 退出课程了
     * @param {*} state
     * @param {*} value
     */
    QUIT_COURSE(state) {
      state.course = null;
      removeSession(tokens.course);
      state.courseID = "";
      removeSession(tokens.courseID);
      state.group = null;
      removeSession(tokens.group);
      state.groupID = null;
      removeSession(tokens.groupID);
      state.groupType = null;
      removeSession(tokens.groupType);

      state.screenQuestion = {};
      state.classPractice = {};
      state.groupDiscussion = {};
    }
  },

  actions: {
    /**
     * 登录请求
     * @param {*} userInfo
     * @returns {Promise<any>}
     */
    LoginByUser({ commit }, userInfo) {
      return new Promise((resolve, reject) => {
        mainHttp.account
          .postLogin(userInfo)
          .then(res => {
            if (res.Result) {
              commit("SET_KEY", res.UserInfo.UserID);
              commit("SET_USER", res.UserInfo.UserName);
              commit("SET_STUDENT", res.Student);
              commit("SET_STUDENTID", res.Student["StudentID"]);
            }
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },

    /**
     * 获取课程服务请求
     * @param {*} classRoomId
     * @returns {Promise<any>}
     */
    GetCourseServiceByUser({ commit }, classRoomId) {
      return new Promise((resolve, reject) => {
        mainHttp.interactConfig
          .getData({ clsRoomID: classRoomId })
          .then(res => {
            if (res.Result) {
              commit("SET_GATEWAYIP", res.Data["InIP"]);
              commit("SET_RECIP", JSON.parse(res.Data["ConfigInfo"]).RecIP);
            }
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },

    /**
     * 获取当前分组
     * @returns {Promise<any>}
     */
    GetGroupByUser({ commit }) {
      return new Promise((resolve, reject) => {
        gatewayHttp.group
          .getList({
            studentID: user.state.studentID
          })
          .then(res => {
            if (res.Result && res.Data) {
              let groupItem = {
                gatewayID: res.Data["GatewayID"],
                groupID: res.Data["GroupID"],
                groupName: res.Data["GroupName"],
                groupMax: res.Data["GroupNumber"],
                groupingID: res.Data["GroupingID"],
                studentID: res.Data["StudentID"],
                currentNum: res.Data["GroupName"]
              };

              let groupIP = res["GatewayIP"] || "";
              commit("SET_GROUPIP", groupIP);
              commit("SET_GROUP", groupItem);
              // if (!user.state.groupID || user.state.groupID !== res.Data["GroupID"]) {
              commit("SET_GROUPID", res.Data["GroupID"]);
              // }
              if (res.GroupingType) commit("SET_GROUPTYPE", res.GroupingType);
            } else {
              commit("QUIT_GROUP");
            }
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },

    /**
     * 加入分组
     * @param {*} groupID
     * @param {*} group
     */
    JoinGroup({ commit }, groupInfo) {
      return new Promise((resolve, reject) => {
        gatewayHttp.group
          .postJoinGroup({
            studentID: user.state.studentID,
            groupID: groupInfo.groupID
          })
          .then(res => {
            if (res.Result) {
              commit("SET_GROUPID", groupInfo.groupID);
              commit("SET_GROUP", groupInfo.group);
              commit("SET_GROUPIP", groupInfo.group["gatewayIP"] || "");
            }
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },

    /**
     * 退出当前分组
     */
    QuitGroup({ commit }) {
      return new Promise((resolve, reject) => {
        gatewayHttp.group
          .postQuitGroup({
            groupID: user.state.groupID,
            studentID: user.state.studentID
          })
          .then(res => {
            if (res.Result) {
              commit("QUIT_GROUP");
              commit("SET_GROUPTYPE", GroupType.Free);
            }
            resolve(res);
          })
          .catch(error => {
            console.log(error);
            reject(error);
          });
      });
    },

    // user logout
    logout({ commit, state }) {
      return new Promise((resolve, reject) => {
        commit("SET_KEY", "");
        commit("SET_STUDENT", null);
        commit("SET_STUDENTID", "");
        removeToken();
        resolve();
      });
    }
  }
};

export default user;



