import Vue from "vue";
import Router from "vue-router";
// import store from "./../store";
import { LimitType } from "@/utils/enum";

import IndexPage from "@/pages/index";

//我的信息
// import personalDatas from "../pages/myDatas/personalDatas.vue";
// import myStats from "../pages/myDatas/myStats.vue";
// import aboutUs from "../pages/myDatas/aboutUs.vue";

import smallClass from "../pages/course/subPages/smallClass.vue";

import csAnswer from "../pages/course/withClass/csAnswer.vue";

Vue.use(Router);

const routes = [
  {
    path: "/",
    redirect: "/login"
  },
  {
    path: "/login",
    meta: { title: "登录", from: "", limits: [LimitType.top] },
    component: () => import("@/pages/user/login")
  },
  {
    path: "/index",
    name: "index",
    component: IndexPage,
    redirect: "/homeCourse",
    children: [
      {
        path: "/homePage",
        name: "homePage",
        meta: { title: "首页", from: "", cache: true, limits: [LimitType.top] },
        component: () => import("@/pages/homePage.vue")
      },
      {
        path: "/homeCourse",
        name: "homeCourse",
        meta: { title: "课程", from: "", cache: true, limits: [LimitType.top] },
        component: () => import("@/pages/homeCourse.vue")
      },
      {
        path: "/myZone",
        name: "myZone",
        meta: { title: "个人中心", from: "", cache: true, limits: [LimitType.top] },
        component: () => import("@/pages/myZone.vue")
      }
    ]
  },
  //我的个人信息
  {
    path: "/personal",
    // name: 'personalDatas',
    component: IndexPage,
    redirect: "/personalDatas",
    children: [
      {
        path: "/personalDatas",
        // name: 'myStats',
        meta: { title: "个人资料", from: "/myZone", limits: [LimitType.top] },
        component: () => import("@/pages/myDatas/personalDatas.vue")
      },
      {
        path: "/helpUse",
        // name: 'myStats',
        meta: { title: "使用说明", from: "/myZone" },
        component: () => import("@/pages/myDatas/helpUse.vue")
      },
      {
        path: "/myStats",
        // name: 'myStats',
        meta: { title: "我的统计", from: "/myZone" },
        component: () => import("@/pages/myDatas/myStats.vue")
      },
      {
        path: "/aboutUs",
        // name: 'aboutUs',
        meta: { title: "关于我们", cache: true, from: "/myZone" },
        component: () => import("@/pages/myDatas/aboutUs.vue")
      }
    ]
  },
  {
    path: "/smallClasssmallClass",
    name: "smallClass",
    meta: { title: "", from: "/course" },
    component: smallClass
  },
  {
    path: "/csAnswer",
    name: "csAnswer",
    component: csAnswer
  },
  { path: "*", redirect: "/" } //异常页面
];

const courseRouter = [
  {
    path: "/course",
    name: "course",
    meta: { title: "课程", from: "/homeCourse", cache: true, limits: [LimitType.inCourse] },
    component: () => import("@/pages/course/course.vue")
  },
  {
    path: "/signIn/:way/:state?",
    name: "signIn",
    meta: { title: "签到", from: "/course", limits: [LimitType.inCourse] },
    component: () => import("@/pages/course/sign/signIn.vue")
  },
  {
    path: "/blackboardWriting",
    name: "boardNote",
    meta: { title: "板书", from: "/course", limits: [LimitType.inCourse] },
    component: () => import("@/pages/course/boardNote/blackboardWriting.vue")
  },
  {
    path: "/groupNote",
    name: "groupNote",
    meta: { title: "小组笔记", from: "/course", limits: [LimitType.inCourse] },
    component: () => import("@/pages/course/groupNote/index.vue")
  },
  {
    path: "/quiz/:selected?",
    name: "quiz",
    meta: { title: "随堂测验", from: "/course", cache: true, limits: [LimitType.inCourse] },
    component: () => import("@/pages/course/quiz/index.vue")
  },
  {
    path: "/screenQuestion/:id?",
    name: "screenQuestion",
    meta: {
      title: "截屏提问",
      from: "/quiz",
      limits: [LimitType.inCourse],
      unAutoNote: true
    },
    component: () => import("@/pages/course/quiz/screenQuestion.vue")
  },
  {
    path: "/classPractice/:id?/:bankId?/:total?/:state?",
    name: "classPractice",
    meta: {
      title: "随堂练习",
      from: "/quiz",
      limits: [LimitType.inCourse],
      unAutoNote: true
    },
    component: () => import("@/pages/course/quiz/classPractice.vue")
  },
  {
    path: "/note",
    name: "note",
    meta: { title: "笔记", from: "/course", cache: true, limits: [LimitType.inCourse] },
    component: () => import("@/pages/course/note/note.vue")
  },
  {
    path: "/noteDetail",
    name: "noteDetail",
    meta: { title: "笔记详情", from: "/note", limits: [LimitType.inCourse] },
    component: () => import("@/pages/course/note/noteDetail.vue")
  },
  {
    path: "/editNote",
    name: "editNote",
    meta: {
      title: "编辑笔记",
      from: "/note",
      limits: [LimitType.inCourse],
      unAutoNote: true
    },
    component: () => import("@/pages/course/note/editNote.vue")
  },
  {
    path: "/question",
    name: "question",
    meta: { title: "提问", from: "/course", limits: [LimitType.inCourse] },
    component: () => import("@/pages/course/question/question.vue")
  },
  {
    path: "/courseware",
    name: "courseware",
    meta: { title: "课件", from: "/course", limits: [LimitType.inCourse] },
    component: () => import("@/pages/course/courseware/index.vue")
  },
  {
    path: "/coursewareView/:id/:fileExt/:name",
    name: "coursewareView",
    meta: { title: "课件预览", from: "/courseware", limits: [LimitType.inCourse] },
    component: () => import("@/pages/course/courseware/fileView.vue")
  },
  {
    path: "/liveLesson",
    name: "liveLesson",
    meta: { title: "课堂实况列表", from: "/course", limits: [LimitType.inCourse] },
    component: () => import("@/pages/course/liveLesson/index.vue")
  },
  {
    path: "/liveLessonType",
    name: "liveLessonType",
    meta: { title: "课堂实况类型", from: "", limits: [LimitType.inCourse, LimitType.afterCourse] },
    component: () => import("@/pages/course/liveLesson/typeList.vue")
  },
  {
    path: "/livePlayback/:type/:index",
    name: "livePlayback/:type/:index",
    meta: { title: "课堂实况回放", from: "", limits: [LimitType.inCourse, LimitType.afterCourse] },
    component: () => import("@/pages/course/liveLesson/playback.vue")
  },

  {
    path: "/groupDiscussion",
    name: "groupDiscussion",
    meta: {
      title: "小组讨论",
      from: "/course",
      limits: [LimitType.inCourse, LimitType.group],
      unAutoNote: true
    },
    component: () => import("@/pages/course/group/groupDiscussion.vue")
  },
  {
    path: "/teamRank",
    name: "teamRank",
    meta: {
      title: "小组风云",
      from: "",
      limits: [LimitType.inCourse, LimitType.group]
    },
    component: () => import("@/pages/course/group/teamRank.vue")
  },
  {
    path: "/joinGroup",
    name: "joinGroup",
    meta: {
      title: "加入小组",
      from: "/course",
      limits: [LimitType.inCourse]
    },
    component: () => import("@/pages/course/group/joinGroup.vue")
  },
  {
    path: "/groupInfo/:groupId?",
    name: "groupInfo",
    meta: {
      title: "小组信息",
      from: "/joinGroup",
      limits: [LimitType.inCourse]
    },
    component: () => import("@/pages/course/group/groupInfo.vue")
  }
];

const courseHistoryRouter = [
  {
    path: "/history",
    name: "history",
    meta: {
      title: "课程",
      from: "/homeCourse",
      cache: true,
      limits: [LimitType.afterCourse]
    },
    component: () => import("@/pages/history/history.vue")
  },
  {
    path: "/history/screenQuestion/:type",
    name: "historyScreen",
    meta: {
      title: "截屏提问",
      from: "/history",
      limits: [LimitType.afterCourse]
    },
    component: () => import("@/pages/history/screenQuestion/index.vue")
  },
  {
    path: "/history/screenQuestionInfo/:id",
    name: "historyScreenInfo",
    meta: { title: "截屏提问内容", from: "", limits: [LimitType.afterCourse] },
    component: () => import("@/pages/history/screenQuestion/Detail.vue")
  },
  {
    path: "/history/classPractise/:id/:bankId/:total",
    name: "historyPractise",
    meta: {
      title: "随堂练习",
      from: "/history",
      limits: [LimitType.afterCourse]
    },
    component: () => import("@/pages/history/classPractise/index.vue")
  },
  {
    path: "/history/groupDiscussion/:id/:groupId/:name",
    name: "historyDiscussion",
    meta: {
      title: "小组讨论",
      from: "/history",
      limits: [LimitType.afterCourse]
    },
    component: () => import("@/pages/history/groupDiscussion/index.vue")
  },
  {
    path: "/history/teamRank/:id/:groupId/:name",
    name: "historyTeamRank",
    meta: { title: "小组讨论", from: "", limits: [LimitType.afterCourse] },
    component: () => import("@/pages/history/groupDiscussion/teamRank.vue")
  },
  {
    path: "/history/courseware",
    name: "historyCourseware",
    meta: { title: "课件", from: "/history", limits: [LimitType.afterCourse] },
    component: () => import("@/pages/history/courseware/index.vue")
  },
  {
    path: "/history/coursewareView/:id/:fileExt/:name",
    name: "historyCoursewareView",
    meta: { title: "课件预览", from: "", limits: [LimitType.afterCourse] },
    component: () => import("@/pages/history/courseware/fileView.vue")
  },
  {
    path: "/history/boardnote",
    name: "historyBoardnote",
    meta: { title: "板书", from: "/history", limits: [LimitType.afterCourse] },
    component: () => import("@/pages/history/boardNote/index.vue")
  },
  {
    path: "/history/groupNote",
    name: "historyGroupNote",
    meta: { title: "小组笔记", from: "/history", limits: [LimitType.afterCourse] },
    component: () => import("@/pages/history/groupNote/index.vue")
  },
  {
    path: "/history/note",
    name: "historyNote",
    meta: { title: "笔记", from: "/history", limits: [LimitType.afterCourse] },
    component: () => import("@/pages/history/note/index.vue")
  },
  {
    path: "/history/note/detail",
    name: "historyNoteDetail",
    meta: { title: "笔记详情", from: "", limits: [LimitType.afterCourse] },
    component: () => import("@/pages/history/note/Detail.vue")
  },
  {
    path: "/history/liveLesson",
    name: "historyLiveLesson",
    meta: { title: "课堂实况", from: "/history", limits: [LimitType.afterCourse] },
    component: () => import("@/pages/history/liveLesson/index.vue")
  },
  {
    path: "/history/question",
    name: "historyQuestion",
    meta: { title: "我的提问", from: "/history", limits: [LimitType.afterCourse] },
    component: () => import("@/pages/history/question/index.vue")
  }
];

